.product-grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  padding: 20px;
  gap: 20px;
}

.product-card {
  border: 1px solid #ddd;
  border-radius: 40px;
  padding: 10px;
  text-align: center;
  width: calc(100% - 40px); /* Default to full width for small screens */
  box-shadow: 0 0 10px rgba(0,0,0,0.1);
  transition: transform 0.3s ease-in-out;
}

/* For screens larger than 600px, display 2 items per row */
@media (min-width: 600px) {
  .product-card {
    width: calc(50% - 40px);
  }
}

/* For screens larger than 900px, display 3 items per row */
@media (min-width: 900px) {
  .product-card {
    width: calc(33.33% - 40px);
  }
}

/* For screens larger than 1200px, display 4 items per row */
@media (min-width: 1200px) {
  .product-card {
    width: calc(25% - 40px);
  }
}

/* For screens larger than 1500px, display 5 items per row */
@media (min-width: 1500px) {
  .product-card {
    width: calc(20% - 40px);
  }
}

.product-card:hover {
  transform: translateY(-10px);
}

.product-card a {
  color: #333;
  text-decoration: none;
  font-weight: 700;
  font-size: 1.2em;
  margin: 10px 0;
  display: block;
}

.product-card button {
  cursor: pointer;
  /* background-color: #ff6347; */
  color: #000000;
  border: none;
  border-radius: 111px;
  padding: 5px 10px;
  text-transform: uppercase;
  font-weight: bold;
  transition: background-color 0.3s ease-in-out;
}

.product-card button.favorite {
  background-color: #8be8ff;
}

.product-card button:hover {
  background-color: #8be8ff;
}

.no-products {
  text-align: center;
  margin-top: 40px;
  font-size: 1.2em;
  font-weight: bold;
  color: #888;
}
