.search-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;
  margin-top: 20px;
  padding: 0 10px;
}

.button-group {
  display: flex;
  justify-content: center;
  margin-top: 10px;
}
  
  .search-input {
    width: 100%;
    max-width: 1000px;
    padding: 10px;
    font-size: 1.2em;
    border-radius: 40px;
  }
  
  .search-button {
    margin-left: 10px;
    padding: 10px 15px;
    font-size: 1.2em;
    color: #000000;
    border: none;
    border-radius: 40px;
    cursor: pointer;
    transition: background-color 0.3s ease-in-out;    
  }
  
  .search-button:hover {
    background-color: #939393;
  }
  
  .voice-search-button {
    background-color: transparent;
    border: none;
    margin-left: 10px;
    font-size: 1.2em;
    color: #888;
    cursor: not-allowed;
  }
  
  .fa-microphone {
    font-size: 1.2em;
  }
  
  .favorites {
    background-color: transparent;
    margin-left: 5px;
    margin-right: 5px;
    padding: 10px 5px;
    font-size: 1.2em;
    border: none;
    border-radius: 40px;
    cursor: pointer;
    transition: background-color 0.3s ease-in-out;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #000000;
    cursor: not-allowed;    
}
.num-favorites {
  margin-right: 5px;
  margin-left: 5px;
}