.sidebar {
  background-color: #f4f4f4;
  padding: 20px;
  width: 200px;
  min-height: 100vh;
  float: left;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  font-family: 'Poppins', sans-serif;    
}

@media (max-width: 800px) {
  .sidebar {
    width: 80%;
    float: none;
    display: none;
  }
}

/* Show sidebar when 'show' class is present */
@media (max-width: 800px) {
  .sidebar.show {
    display: block;
  }
}

.sidebar h2 {
  margin-bottom: 10px;
  font-size: 1.5em;
  font-weight: bold;
  font-family: 'Poppins', sans-serif;
}

.sidebar ul {
  padding: 0;
  margin: 0;
  list-style: none;
  display: grid;
  gap: 10px;
}

.sidebar li {
  margin-bottom: 5px;
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 4px;
  padding: 10px;
  box-shadow: 0 0 10px rgba(0,0,0,0.1);
  }

.sidebar a {
  color: #333;
  text-decoration: none;
  font-weight: 500;
  display: block;
  transition: color 0.3s ease-in-out;
}

.sidebar a:hover {
  color: #ff4500;
}

.sidebar p {
  color: #888;
  margin-top: 10px;
  font-size: 1.1em;
}

.buy-all-button {
  display: block;
  margin-top: 20px;
  background-color: #8be8ff;
  color: #fff;
  border: none;
  border-radius: 4px;
  padding: 10px 15px;
  font-size: 1.2em;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.3s ease-in-out;
  cursor: not-allowed;
}
